.section {
  position: relative;
  @include cover;
  background-size: auto;
  background-position: 0 100%;
  @include md {
    background-size: 60%; }
  &__title {
    text-align: center;
    @include lg {
      margin-bottom: 30px; } }
  &__title-left {
    max-width: 515px;
    margin-right: percentage(1/12); }
  &__layer {
    position: absolute;
    z-index: 0;
    left: 0;
    right: 0;
    top: 0;
    // background-color: $white
    @include flex(flex-end, normal, column);
    @include before {
      position: absolute;
      z-index: -1;
      background-color: $white;
      left: 0;
      right: 0;
      top: 0;
      bottom: 45px; } }
  &--black {
    background-color: $black; } }

.section-img {
  position: absolute;
  z-index: 0;
  pointer-events: none;
  &.is-left {
    left: 0; }
  &.is-bottom {
    bottom: 0; }
  &.is-bottom-100 {
    bottom: 10%; }
  &.is-right {
    right: 0;
    text-align: right; }
  &.is-top-center {
    top: 50%;
    transform: translate(0, -50%); }
  &.is-top {
    top: 0; }
  &.is-top-100 {
    top: 10%; }
  &.is-top-200 {
    top: 20%; } }

.section-title {
  $this: &;
  position: relative;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
  &__title {
    @include image('border-title.png');
    @include cover;
    background-size: 100%;
    @include css-lock(10, 50, 320, 1499, padding); }
  &__text {
    transform: translate(0, -50%);
    @include lg {
      transform: translate(0, 0);
      margin-top: -1em; } }

  &--light {
    #{$this}__title {
      @include image('border-light.png'); } } }

.s-hits {
  .section__layer {
    @include css-lock(20, 135, 320, 1499, height);
    @include before {
      @include css-lock(5, 45, 320, 1499, bottom); } }
  .section__title {
    margin-bottom: 65px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } } }

.s-vine {
  .section-title {
    margin-bottom: 90px;
    @include xl {
      margin-bottom: 70px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 15px; } }
  .section-img {
    &.is-top-100.is-right {
      @include xl {
        img {
          max-width: 80%; } }
      @include lg {
        display: none; } }
    &.is-top-100.is-left {
      z-index: 0;
      @include xl {
        img {
          max-width: 80%; } }
      @include lg {
        img {
          max-width: 70%; } } } } }

.s-about {}

.s-features {
  background-size: 25%;
  @include md {
    background-size: 25%; }
  .container {
    // position: relative
 }    // z-index: 1
  .section-img {
    &.is-left.is-top-center {
      z-index: 0; } } }

.s-features-item {
  min-height: 80vh; }

.s-delivery {
  .section__title {
    max-width: 565px;
    margin-left: auto;
    margin-right: auto; } }

.s-gallery {
  padding-top: 15%;
  padding-bottom: 15%;
  .container {
    position: relative;
    z-index: 1; }
  .section-img {
    &.is-top.is-right {
      @include xl {
        img {
          max-width: 80%; } }
      @include lg {
        img {
          max-width: 60%; } }
      @include sm {
        z-index: 0;
        img {
          max-width: 50%; } } } }
  .section__title {
    max-width: 605px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 70px;
    .title {
      margin-bottom: 25px; } } }

.s-contacts {
  .section__title {
    max-width: 705px;
    margin-left: auto;
    margin: auto;
    margin-bottom: 30px; } }
