.contacts-tabs {
  &__nav {
    @include flex(space-around, center, row, wrap);
    margin-bottom: 35px; }
  &__btn {
    display: block;
    margin-left: 10px;
    margin-right: 10px;
    background-color: transparent;
    color: $gray-light;
    @include light;
    @include tr(color .3s, transform .3s);
    &.is-active {
      color: $black;
      @include scale(1.4);
      @include lg {
        @include scale(1);
        font-size: 1.2em; } }
    @include hover {
      @extend .contacts-tabs__btn.is-active; } }
  &__item {
    max-height: 0;
    overflow: hidden;
    &.is-active {
      max-height: none;
      overflow: visible;
      .contacts {
        &__img {
          display: block;
          opacity: 1; }
        &__block {
          .contact, .contacts__block-btn {
            opacity: 1; } } } } } }

.contacts {
  position: relative;
  @include flex(flex-end);
  @include sm {
    margin-bottom: 40px; }
  &__img {
    @include cover;
    padding-top: 40%;
    width: percentage(10/12);
    position: relative;
    margin-right: 50px;
    border-radius: 15px;
    display: none;
    @include md {
      padding-top: 50%;
      margin-right: 0;
      width: 100%; }
    @include xs {
      padding-top: 80%; }
    @include xss {
      padding-top: 100%; } }
  &__block {
    background-color: $white;
    border-radius: 15px;
    box-shadow: 0px 0px 35px 0px rgba(0, 0, 0, 0.16);
    text-align: center;
    // padding: 50px 15px 45px
    padding: 15px;
    @include flex(center, center, column);
    position: absolute;
    width: percentage(3/12);
    top: 30px;
    bottom: 30px;
    left: 0;
    z-index: 2;
    .contact, .contacts__block-btn {
      @include tr(opacity .5s);
      opacity: 0; }
    @include lg {
      width: percentage(4/12); }
    @include md {
      width: 50%; }
    @include sm {
      width: 100%;
      max-width: 400px;
      transform: translate(-50%, 0);
      left: 50%;
      top: auto;
      bottom: -40px; } } }

.contact {
  margin-bottom: 30px;
  @include md {
    margin-bottom: 15px; }
  &__title {
    @include semi;
    color: $gray-dark; } }

