.hero {
  position: relative;
  .section-img {
    z-index: 1;
    @include xl {
      img {
        max-width: 80%; } }
    @include lg {
      display: none; } }
  &__content {
    max-width: 630px; }
  &__title, &__btns {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } }
  &__text {
    margin-bottom: 50px;
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 15px; } }
  &__btns {
    @include flex;
    @include md {
      display: block; } }
  &__btn {
    @include notlast {
      margin-right: 15px;
      @include md {
        margin-right: 0;
        margin-bottom: 10px; } } }
  &__del {
    color: $white;
    .icon {
      font-size: 45px;
      margin-right: 10px;
      vertical-align: middle; } }


  .hero-slide {
    @include cover;
    &__inner {
      @include flex(center, normal, column);
      padding-top: 10%;
      padding-bottom: 20%;
      @include r(1600) {
        padding-left: 45px;
        padding-right: 45px; }
      @include xs {
        padding-left: 0;
        padding-right: 0; } } } }
