.about {}

.about-block {
  $this: &;
  position: relative;
  @include cover;
  background-size: auto;
  background-position: 0 100%;
  @include md {
    margin-bottom: 15px;
    background-size: 60%; }
  &:nth-child(1) {
    #{$this}__text {
      top: 10%;
      left: 15%;
      max-width: 595px; } }
  &:nth-child(2) {
    margin-top: -20px;
    @include flex(flex-end);
    @include md {
      margin-top: 0; }
    @include sm {
      margin-bottom: 55px; }
    #{$this}__text {
      max-width: 835px;
      top: 50%;
      transform: translate(0, -50%);
      right: 30%;
      @include sm {
        right: auto;
        left: 50%;
        transform: translate(-50%, 0);
        top: auto;
        bottom: -40px;
        width: 90%; } }
    #{$this}__img {
      img[data-loaded="true"] {
        position: relative;
        right: -20%;
        @include md {
          right: 0; } }
      @include xs {
        height: 400px;
        img {
          object-fit: cover;
          width: 100%;
          height: 100%; } } } }
  &:nth-child(3) {
    @include flex(flex-start, center);
    #{$this}__img {
      width: percentage(5/12);
      text-align: center;
      img {
        display: inline-block; } }
    #{$this}__qoute-fu {
      width: percentage(7/12); } }
  &:nth-child(4) {
    @include flex(flex-end);
    margin-top: -100px;
    @include xl {
      margin-top: -80px; }
    @include lg {
      margin-top: -60px; }
    @include md {
      margin-top: -50px; }
    @include sm {
      margin-top: 0;
      margin-bottom: 40px; }
    #{$this}__quote {
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      right: 40%;
      @include md {
        right: auto;
        left: 0;
        max-width: 90%; }
      @include sm {
        top: auto;
        left: 50%;
        transform: translate(-50%, 0);
        bottom: -40px;
        width: 90%; } }
    #{$this}__img {
      @include xs {
        height: 400px;
        img {
          height: 100%;
          width: 100%;
          object-fit: cover; } } } }
  &__img {
    // +cover
    // pt
    border-radius: 15px;
    position: relative;
    z-index: 1;
    img {
      border-radius: 15px; } }
  &__img-right {
    position: absolute;
    z-index: 0;
    top: -20px;
    right: 0;
    img[data-loaded="true"] {
      position: relative;
      right: -30%; } }
  &__text {
    position: absolute;
    z-index: 2;
    box-shadow: 0px 3px 57px 0px rgba(0, 0, 0, 0.11);
    background-color: $white;
    border-radius: 15px;
    padding: 45px 15px 40px 50px;
    @include xl {
      padding: 35px 15px 35px 35px; }
    @include lg {
      padding: 30px 15px 30px 30px; }
    @include sm {
      padding: 15px; }
    .title {
      margin-bottom: 20px;
      @include md {
        margin-bottom: 10px; } }
    p {
      @include notlast {
        margin-bottom: 30px;
        @include md {
          margin-bottom: 15px; } } } }
  &__quote {
    max-width: 730px;
    position: relative;
    z-index: 2; }
  &__quote-fu {
    max-width: 770px; } }
